.con{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  background-color: #eaeaeb;
}

.menu-btn{
  position: fixed;
  top: 2%;
  left: 0%;
  border: 1px solid transparent;
  border-radius: 10px;
  font-weight: 800;
  width: auto;
  height:auto;
  padding: 5px 20px 5px 20px;
  background-color: rgb(228, 227, 225);
  color: gray;
  cursor: pointer;
  visibility: hidden;

}
.menu-btn:hover{
  padding: 7px 22px 7px 22px;
  box-shadow: 2px 6p 10px 1px gray;
}

.menu{
position: fixed;
top: 0;
left: 0;
width: 17%;
height: 100%;
background-color: #04043c;
padding-right: 10px;
padding-left: 5px;
visibility: hidden;
}
.header{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content:left;
  margin-left: 20px;
  gap:10px;
  background-color: #04043c;
}
.header .img{
  width: 33px;
  height: 30px;
  padding-top: 16.8px;
  padding-right: 0;
}
.txt{
  color: white;
  font-weight: 500;
}
.mt{

  color: rgb(152, 154, 155);
  padding: 10px;
  border-radius: 10px;
  margin-top: 10px;
  width: 80%;
}
.mt:hover{
  background-color: white;
  color:#0e0e8f;
  cursor: pointer;
  font-weight: 700;
}
.dash{
  margin-top: 40px;
  background-color: #2b2b68;
  color: white;
}
.menu-bar{
  position: relative;
  display: flex;
  flex-direction: column;

}
.mt span{
  position: absolute;
  float: right;
  right: 18%;
  font-weight: 800;

}
.profile{
  border-radius: 10px;
  padding: 20px 10px 10px 60px;
  line-height: 3px;
  position: absolute;
  margin-bottom: 100px;
  bottom: 0;
  width: 70%;

}
.profile span{

  position: absolute;
  float: right;
  right: 10%;
  font-weight: 800;

}
.profile p{
  color: gray;
  font-size: 15px;
}
.profile img{
  position: absolute;
  top: 25%;
  left: 10%;
}
.profile:hover{
  cursor: pointer;
  background-color:#020227;
}

.row{
  margin-left: 25%;
  position: relative;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  border: 0px solid black;
  margin-top: -3px;
}

#header{
  z-index: 30;
}
#header div{
  z-index: 40;
}

.hd{
  margin-left:0;
  border: 0px solid transparent;
}

.one{
  margin-top: 16px;
  border: 0px solid white;
  justify-content: flex-start;
  gap: 40px;

}
.one .box{
  width: 210px;
  height: 150px;
  border-radius: 10px;
  background-color: white;
  border: 1px solid white;
  box-shadow: 1px 1px 2px 1px white;
  transition: all 0.5s;
  cursor: pointer;
}
.one .box:hover{
  padding: 5px;
}
.box .round{
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background-color: rgb(232, 239, 239);
  margin-top: 10%;
  margin-left: 10%;
  display: flex;
  overflow: hidden;
}

.box .r2{
  background-color: rgb(224, 220, 225);
}
.box .r3{
  background-color: rgb(194, 251, 255);
}

.box .r4{
  background-color: rgb(246, 192, 203);
}

.round .img{
  justify-content: center;
  width: 70px;
  height: 70px;
  margin-left: 7%;
  margin-top: 4%;
}

.two{
  margin-top: 50px;
  justify-content: flex-start;
  gap: 50px;
}
.two .box{
  width: 300px;
  height: 250px;
  background-color: rgb(242, 242, 242);

}
.two .b1{
  width: 630px;
}
.two .b1 h4{
margin-top: -10px;

}

.t1{
  margin-top: -70px;
  margin-left: 110px;
  color: rgb(191, 190, 190);
}


.hdr .box{
  width: 100%;
  height: 50px;
  background-color: #f7f7f9;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  gap: 10px;
  overflow: hidden;

}
.hdr .box h3{
  font-weight: 500;
}
.hdr .box .img{
  width: 30px;
  height:30px;
  margin-top: 2%;

}
.hdr .box div{
  position: absolute;
  float: right;
  right: 5%;
  margin-top: 20px;

}
.hdr .box div input{
border: 1px solid transparent;
text-align: center;
padding: 5px;
border-radius: 10px;
box-shadow: 2px 4px 6px 2px white;
background-color: #eaeaeb;

}
.b1{
  padding: 10px;
}

.box .pie{
  position: relative;
  bottom: 0;
  width: 100%;
  height: 80%;
  margin-top: 0px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;


}
.box .pie .pie-bar{


width: 50px;
position: relative;
transform: rotate(180deg);

}

.pie-bar .range{
  position: absolute;
  top: 15%;
  width: 80%;
  height: 80%;
  background-color: rgb(216, 215, 215);
  border-radius: 10px;
}
.pie-bar p{
  position: absolute;
  top: -7%;
  color: gray;
  margin-left: 10px;
  transform: rotate(180deg);
}

.b2{
  display: flex;
}
.b2 img{
  justify-content: center;
  width: 210px;
  height: 190px;
  align-items: center;
  margin-top: 15%;
  margin-left: 15%;
}

.b2 h4{
  position: absolute;
  margin-top: 13px;
  margin-left: 0px;
}
.b2 p{
  position: absolute;
  margin-top: 35px;
  margin-left: 0px;
  width: auto;
  color: rgb(154, 154, 154);
}

.three .b1{

  width: 100%;

}

table{
  margin-left: -30px;
}

td{
  width: 100px;
  height: auto;
  background-color: #f0f0f0;
  padding: 10px;
}
td p{
  color: rgb(180, 179, 179);
}

.c1{
  width: 600px;
}

.blk{
  color: #292929;
}


@media screen and (max-width:3280px) {
  
  .menu{
    width: 20%;
    visibility: visible;
  }
  .menu-btn{
    visibility: hidden;
  }


}


@media screen and (max-width:700px) {
  
  .menu{
    width: 50%;
    visibility: hidden;
  }
  .menu-btn{
    visibility: visible;
  }


}

@media screen and (max-width:346px) {
  
  .menu{
    width: 100%;
    visibility: hidden;
  }
  .menu-btn{
    visibility: visible;
  }


}